___CSS_LOADER_EXPORT___.push([module.id, ".material-design-icon[data-v-395fa6ac]{display:flex;align-self:center;justify-self:center;align-items:center;justify-content:center}li.active[data-v-395fa6ac]{background-color:var(--color-background-hover)}.action--disabled[data-v-395fa6ac]{pointer-events:none;opacity:.5}.action--disabled[data-v-395fa6ac]:hover,.action--disabled[data-v-395fa6ac]:focus{cursor:default;opacity:.5}.action--disabled *[data-v-395fa6ac]{opacity:1 !important}.action-checkbox[data-v-395fa6ac]{display:flex;align-items:flex-start;width:100%;height:auto;margin:0;padding:0;cursor:pointer;white-space:nowrap;color:var(--color-main-text);border:0;border-radius:0;background-color:transparent;box-shadow:none;font-weight:normal;line-height:44px}.action-checkbox__checkbox[data-v-395fa6ac]{position:absolute;top:auto;left:-10000px;overflow:hidden;width:1px;height:1px}.action-checkbox__checkbox:focus+.action-checkbox__label[data-v-395fa6ac]{opacity:1}.action-checkbox__label[data-v-395fa6ac]{display:flex;align-items:center;width:100%;padding:0 !important;padding-right:14px !important;opacity:.7}.action-checkbox__label[data-v-395fa6ac]::before{margin:0 14px 0 !important}.action-checkbox--disabled[data-v-395fa6ac],.action-checkbox--disabled .action-checkbox__label[data-v-395fa6ac]{cursor:pointer}.action-checkbox:not(.action-checkbox--disabled):hover .action-checkbox__label[data-v-395fa6ac],.action-checkbox:not(.action-checkbox--disabled):focus .action-checkbox__label[data-v-395fa6ac]{opacity:1}\n", "",{"version":3,"sources":["webpack://./../../assets/material-icons.css","webpack://./../../assets/action.scss","webpack://./../../assets/variables.scss","webpack://./ActionCheckbox.vue"],"names":[],"mappings":"AAGA,uCACC,YAAa,CACb,iBAAkB,CAClB,mBAAoB,CACpB,kBAAmB,CACnB,sBAAuB,CACvB,2BCiBE,8CAA+C,CAC/C,mCAMD,mBAAoB,CACpB,UCQmB,CDVpB,kFAIE,cAAe,CACf,UCKkB,CDVpB,qCAQE,oBAAqB,CEiHxB,kCACC,YAAa,CACb,sBAAuB,CAEvB,UAAW,CACX,WAAY,CACZ,QAAS,CACT,SAAU,CAEV,cAAe,CACf,kBAAmB,CAEnB,4B